import React, { FC } from "react";
import { graphql, HeadFC, PageProps } from "gatsby";
import type { FsUnboxIndexPageQuery } from "../../../gatsby-types";
import ogImage from "../../../images/ogimage.png";
import { fsUnboxIndex } from "../../../components/fs/unbox/index";

type siteMetadata = {
  title: string;
  descriptiion: string;
  siteUrl: string;
};

export const query = graphql`
  query FsUnboxIndex {
    site {
      siteMetadata {
        title
        description
        siteUrl
      }
    }
  }
`;

const FsUnboxIndexPage: FC<PageProps<FsUnboxIndexPageQuery>> = ({ data }) => {
  return fsUnboxIndex();
};

export default FsUnboxIndexPage;

export const Head: HeadFC<PageProps<FsUnboxIndexPageQuery>> = ({ data }) => {
  const siteMetadata: siteMetadata = data.site.siteMetadata;
  return (
    <>
      <html lang="ja" />
      <title>{siteMetadata.title}</title>
      <meta charSet="utf-8" />
      <meta name="viewport" content="width=device-width, initial-scale=1.0" />
      <meta property="og:title" content={siteMetadata.title} />
      <meta property="og:description" content={siteMetadata.descriptiion} />
      <meta property="og:image" content={siteMetadata.siteUrl + ogImage} />
      <meta property="og:url" content={siteMetadata.siteUrl} />
      <meta property="og:type" content="website" />
      <meta property="og:site_name" content={siteMetadata.title} />
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:site" content="@twilightplc" />
      <link rel="canonical" href={siteMetadata.siteUrl} />
      <body />
    </>
  );
};
